.metaverse {
  width: 860px;
  margin: auto;
  @media (max-width: 768px) {
    width: 100%;
    padding: 15px;
  }
  .metaverse_icon {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @media (max-width: 768px) {
      // margin-right: auto;
    }
    > :nth-child(2n) {
      @media (max-width: 768px) {
        margin-right: 0;
      }
    }
    > div {
      margin-top: 20px;
      text-align: center;

      > :first-child {
        width: 138px;
        height: 138px;
        border-radius: 50%;
        background-color: #f0f0f0;
        line-height: 138px;
        margin-bottom: 20px;
        overflow: hidden;
      }
      > :nth-child(2) {
        font-weight: bold;
      }
    }
  }
  .metaverse_title {
    margin-top: 70px;
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 25px;
  }
  > :nth-child(4) {
    text-align: left !important;
    > div {
      line-height: 32px;
    }
    > :last-child {
      margin-top: 40px;
    }
  }
}

.metaverse_half {
  display: flex;
  @media (max-width: 768px) {
    margin-top: 80px;
    flex-direction: column-reverse;
  }
  > div {
    width: 50%;
    min-height: 800px;
    @media (max-width: 768px) {
      width: 100%;
      height: 550px;
      min-height: unset;
    }
    &:first-child {
      background-color: rgba(30, 30, 30, 0.03);
      padding-top: 330px;
      @media (max-width: 768px) {
        padding: 0;
      }
      > div {
        width: 70%;
        margin: auto;
        @media (max-width: 768px) {
          width: 100%;
          padding: 0 15px;
          margin-top: 30px;
        }
      }
      .title {
        font-size: 22px;
        font-weight: bolder;
        margin-bottom: 30px;
        // @media (max-width: 768px) {
        //   font-size: 18px !important;
        // }
      }
    }
    &:nth-child(2) {
      background: url(../../assets/imgs/imgs/decentralized_bg1.png) center /
        cover no-repeat;
      > img {
        margin-left: 50%;
        transform: translateX(-50%);
        &:nth-child(2) {
          margin-bottom: 170px;
          @media (max-width: 768px) {
            width: 80px;
          }
        }
        @media (max-width: 768px) {
          margin-top: 40px;
          width: 250px;
        }
      }
    }
  }
}
